<template>
  <ul class="nav navbar-nav">
    <li class="dropdown">
      <a class="dropdown-toggle" data-toggle="dropdown" role="button">
        <i class="fa fa-pencil-square"></i>  <b class="caret"></b>
      </a>
      <ul class="dropdown-menu">
        <li class="dropdown-header"></li>
        <li><a @click="selectTemplate('useCase')"></a></li>
        <!--<li><a @click="selectTemplate('ActivityB')"> &#946;</a></li>-->
        <li><a @click="selectTemplate('Activity')"></a></li>
        <li><a @click="selectTemplate('Sequence')"></a></li>
        <li class="divider"></li>
        <li class="dropdown-header"></li>
        <li><a @click="selectTemplate('Object')"></a></li>
        <li><a @click="selectTemplate('Class')"></a></li>
        <li><a @click="selectTemplate('ER')">ER </a></li>
        <li class="dropdown-header"></li>
        <li><a @click="selectTemplate('State')"></a></li>
        <li><a @click="selectTemplate('Component')"></a></li>
        <li><a @click="selectTemplate('Deployment')"></a></li>
        <li><a @click="selectTemplate('Archimate')"></a></li>
        <!--<li><a @click="selectTemplate('Object')">UI</a></li>-->


      </ul>
    </li>
  </ul>
</template>

<script>
/* @flow */

export default {
  name: 'umlTemplate',
  data(): any {
    return {
      selectMessage: '?'
    }
  },
  methods: {
    selectTemplate(prop: string) {
      if (window.confirm(this.selectMessage)) {
        this.$store.dispatch('umlTemplate/selectTemplate', prop)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dropdown-menu > li > a {
  cursor: pointer;
}
</style>



